import { Component, OnInit } from '@angular/core';
import {ProdInv} from './models/prod-inv';
import {ProdInvService} from './service/prod-inv.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [ProdInvService]
})
export class AppComponent {
  prodInv : ProdInv[];

  constructor(private prodInvService : ProdInvService){

  }
  public ngOnInit(){
    this.prodInvService.getAllProducts().subscribe(
      prodInv =>{
        this.prodInv = prodInv;
      }
    )
  }
}



